/********  #Section3   **********/


.about #Sec3 .sect-inner {
    width: 80%;
    position: relative;
    margin: 0 auto;
    padding: 40px 0;
}
  
  
.about #Sec3 .sect-inner .sect-title{
    text-align: center;
    font-size: 40px;
    font-weight: bold;
    padding-top: 20px;
}
  
.about #Sec3 .sect-inner .sect-content {
  display: flex;
  justify-content: center;
  margin-top: 30px; flex-wrap: wrap;
}
.about  .sect-content .card{
  position: relative;
  display: flex;
  justify-content: center; align-items: center;
  flex-basis: 25%; height: 180px; text-align: center;
}
.about .sect-content .card .percent{
  position: relative;
  width: 150px; height: 150px;
  border-radius: 50%;
  z-index: 10;
}

.about .percent svg{
  position: relative; width: 150px; 
  height: 150px; z-index: 100;
}

.about .percent svg circle{
  width: 100%; height: 100%; fill: none;
  stroke: #ececec;
  stroke-width: 10;
  stroke-linecap: round;
  transform: translate(5px, 5px);
  stroke-dasharray: 440;
}

.card:nth-child(1) .percent svg circle{
  stroke-dashoffset: calc(440 - (440*90) / 100);
}
.card:nth-child(2) .percent svg circle{
  stroke-dashoffset: calc(440 - (440*90) / 100);
}
.card:nth-child(3) .percent svg circle{
  stroke-dashoffset: calc(440 - (440*90) / 100);
}
.card:nth-child(4) .percent svg circle{
  stroke-dashoffset: calc(440 - (440*90) / 100);
}
.card:nth-child(5) .percent svg circle{
  stroke-dashoffset: calc(440 - (440*90) / 100);
}
.card:nth-child(6) .percent svg circle{
  stroke-dashoffset: calc(440 - (440*90) / 100);
}

.about .percent .data{
  position: absolute;
  top: 0; left: 0;
  height: 100%; width: 100%;
  display: flex; justify-content: center;
  align-items: center;
  flex-direction: column;
}
.percent .data .number{
  font-size: 24px; color: #777;
}
.percent .data .number span{
  font-size: 14px;
}
.percent .data .text{
  font-size: 16px; color: #777;
}

.stickbar-wrap{
  display: flex;
  width: 100%;
  padding: 40px 0;
}

.stickbar-wrap .stickBar{
  flex-basis: 46%;
}
.stickbar-wrap .stickBar:nth-child(1){
  margin-right: 8%;
}

.stickbar-wrap .stickBar li{
  position: relative;
  width: 100%;
  display: flex;
  margin-bottom: 18px;
}
.stickbar-wrap .stickBar li h4{
  flex-basis: 30%;
  color: #777;
  padding-right: 20px;
  text-align: right;
}
.stickbar-wrap .stickBar .bar {
  flex-basis: calc(70% - 20px);
  background: #eeeeee;
  display: block;
  height: 10px;
  width: calc(70% - 20px);
  border-radius: 5px;
  overflow: hidden;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}

.stickbar-wrap .stickBar .bar .per{
  position: absolute;
  top: 0; left: 0;
  background: #888;
  border-radius: 5px;
  height: 10px;
  
}
.bar01{
  width: 90%;
}

.stickBar>.bar>.bar02{
  width: 94%;
}

.stickBar>.bar>.bar03{
  width: 87%;
}


